*{
  margin:0;
  padding: 0;
}
body{
  background-color: gold;
}
.app{
  text-align: center;
  color: steelblue;
}
h1{
  color: tomato;
}
.app input{
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app button{
  color: rgb(117, 71, 10);
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
}
.navActive{
  color: rgb(171, 23, 23);
  font-size: 25px;
  font-weight: bold;
}
nav{
  background-color: wheat;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  color: rgb(42, 16, 146);
  font-weight: bold;
}
nav ul li{
  cursor: pointer;
  font-size: 15px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.2);
}